        { name: 'hacker', label: 'ÈªëÂÆ¢È£éÊ†º (Áªø/Á∫¢)' },
        { name: 'cyberpunk', label: 'ËµõÂçöÊúãÂÖã (Á¥´/Ëìù)' },
        { name: 'retro', label: 'Â§çÂè§È£éÊ†º (Áê•ÁèÄËâ≤)' },
        { name: 'neon', label: 'ÈúìËôπÈ£éÊ†º (Â§öÂΩ©)' }
      ],
      
      quickCommands: [
        { name: 'Á≥ªÁªüÊâ´Êèè', command: 'scan 192.168.1.1' },
        { name: 'ÈªëÂÆ¢ÊîªÂáª', command: 'hack pentagon.gov' },
        { name: 'Áü©ÈòµÊïàÊûú', command: 'matrix' },
        { name: 'Á≥ªÁªü‰ø°ÊÅØ', command: 'system' },
        { name: '‰∏ãËΩΩÊñá‰ª∂', command: 'download secret.zip' },
        { name: 'Âä†ÂØÜÊï∞ÊçÆ', command: 'encrypt "top secret"' }
      ],
      
      demoCommands: [
        'system',
        'scan 192.168.1.1',
        'hack mainframe.corp.com',
        'download classified.zip',
        'encrypt "mission accomplished"',
        'matrix'
      ]
    }
  },
  
  mounted() {
