  },
  
  methods: {
    initTerminal() {
      this.addLine('welcome', `
        <div class="welcome-banner">
          ‚ñà‚ñà‚ïó  ‚ñà‚ñà‚ïó ‚ñà‚ñà‚ñà‚ñà‚ñà‚ïó  ‚ñà‚ñà‚ñà‚ñà‚ñà‚ñà‚ïó‚ñà‚ñà‚ïó  ‚ñà‚ñà‚ïó‚ñà‚ñà‚ñà‚ñà‚ñà‚ñà‚ïó  ‚ñà‚ñà‚ñà‚ñà‚ñà‚ñà‚ïó ‚ñà‚ñà‚ïó  ‚ñà‚ñà‚ïó
          ‚ñà‚ñà‚ïë  ‚ñà‚ñà‚ïë‚ñà‚ñà‚ïî‚ïê‚ïê‚ñà‚ñà‚ïó‚ñà‚ñà‚ïî‚ïê‚ïê‚ïê‚ïê‚ïù‚ñà‚ñà‚ïë ‚ñà‚ñà‚ïî‚ïù‚ñà‚ñà‚ïî‚ïê‚ïê‚ñà‚ñà‚ïó‚ñà‚ñà‚ïî‚ïê‚ïê‚ïê‚ñà‚ñà‚ïó‚ïö‚ñà‚ñà‚ïó‚ñà‚ñà‚ïî‚ïù
          ‚ñà‚ñà‚ñà‚ñà‚ñà‚ñà‚ñà‚ïë‚ñà‚ñà‚ñà‚ñà‚ñà‚ñà‚ñà‚ïë‚ñà‚ñà‚ïë     ‚ñà‚ñà‚ñà‚ñà‚ñà‚ïî‚ïù ‚ñà‚ñà‚ñà‚ñà‚ñà‚ñà‚ïî‚ïù‚ñà‚ñà‚ïë   ‚ñà‚ñà‚ïë ‚ïö‚ñà‚ñà‚ñà‚ïî‚ïù
          ‚ñà‚ñà‚ïî‚ïê‚ïê‚ñà‚ñà‚ïë‚ñà‚ñà‚ïî‚ïê‚ïê‚ñà‚ñà‚ïë‚ñà‚ñà‚ïë     ‚ñà‚ñà‚ïî‚ïê‚ñà‚ñà‚ïó ‚ñà‚ñà‚ïî‚ïê‚ïê‚ñà‚ñà‚ïó‚ñà‚ñà‚ïë   ‚ñà‚ñà‚ïë ‚ñà‚ñà‚ïî‚ñà‚ñà‚ïó
          ‚ñà‚ñà‚ïë  ‚ñà‚ñà‚ïë‚ñà‚ñà‚ïë  ‚ñà‚ñà‚ïë‚ïö‚ñà‚ñà‚ñà‚ñà‚ñà‚ñà‚ïó‚ñà‚ñà‚ïë  ‚ñà‚ñà‚ïó‚ñà‚ñà‚ñà‚ñà‚ñà‚ñà‚ïî‚ïù‚ïö‚ñà‚ñà‚ñà‚ñà‚ñà‚ñà‚ïî‚ïù‚ñà‚ñà‚ïî‚ïù ‚ñà‚ñà‚ïó
          ‚ïö‚ïê‚ïù  ‚ïö‚ïê‚ïù‚ïö‚ïê‚ïù  ‚ïö‚ïê‚ïù ‚ïö‚ïê‚ïê‚ïê‚ïê‚ïê‚ïù‚ïö‚ïê‚ïù  ‚ïö‚ïê‚ïù‚ïö‚ïê‚ïê‚ïê‚ïê‚ïê‚ïù  ‚ïö‚ïê‚ïê‚ïê‚ïê‚ïê‚ïù ‚ïö‚ïê‚ïù  ‚ïö‚ïê‚ïù
        </div>
      `)
      this.addLine('info', 'Ê¨¢Ëøé‰ΩøÁî® HackBox Terminal v2.0')
      this.addLine('info', 'ËæìÂÖ• "help" Êü•ÁúãÂèØÁî®ÂëΩ‰ª§')
      this.addLine('info', 'ÂáÜÂ§áÂºÄÂßã‰Ω†ÁöÑÈªëÂÆ¢‰πãÊóÖ...')
      this.addLine('separator', '‚ïê'.repeat(60))
    },

    addLine(type, content, showCursor = false) {
      this.outputLines.push({
        type,
        content,
        showCursor
      })
      this.$nextTick(() => {
        this.scrollToBottom()
      })
    },

    async typeText(type, text, showCursor = true) {
      if (this.isTyping) return
      this.isTyping = true

      const line = { type, content: '', showCursor: true }
      this.outputLines.push(line)

      for (let i = 0; i < text.length; i++) {
        line.content += text[i]
        await this.sleep(this.typeSpeed)
        this.$nextTick(() => {
          this.scrollToBottom()
        })
      }

      line.showCursor = showCursor
      this.isTyping = false
    },

    sleep(ms) {
      return new Promise(resolve => setTimeout(resolve, ms))
    },

    scrollToBottom() {
      const terminalBody = this.$refs.terminalBody
      if (terminalBody) {
        terminalBody.scrollTop = terminalBody.scrollHeight
      }
    },

    focusInput() {
      this.$nextTick(() => {
        if (this.$refs.commandInput) {
          this.$refs.commandInput.focus()
        }
      })
    },

    executeCommand() {
      if (this.isTyping || !this.currentCommand.trim()) return

      const command = this.currentCommand.trim()
      this.addLine('command', command)
      this.commandHistory.unshift(command)
      this.historyIndex = -1
      this.currentCommand = ''

      this.processCommand(command)
    },

    executeQuickCommand(command) {
      if (this.isTyping) return
      this.currentCommand = command
      this.executeCommand()
    },

    async processCommand(command) {
      const [cmd, ...args] = command.toLowerCase().split(' ')

      switch (cmd) {
        case 'help':
          await this.showHelp()
          break
        case 'clear':
          this.clearTerminal()
          break
        case 'hack':
          await this.simulateHack(args.join(' ') || 'target.com')
          break
        case 'scan':
          await this.simulateScan(args.join(' ') || '192.168.1.1')
          break
        case 'matrix':
          await this.showMatrix()
          break
        case 'system':
          await this.showSystemInfo()
          break
        case 'download':
          await this.simulateDownload(args.join(' ') || 'file.zip')
          break
        case 'encrypt':
          await this.simulateEncrypt(args.join(' ') || 'data')
          break
        default:
          this.addLine('error', `ÂëΩ‰ª§Êú™ÊâæÂà∞: ${cmd}`)
          this.addLine('info', 'ËæìÂÖ• "help" Êü•ÁúãÂèØÁî®ÂëΩ‰ª§')
      }
    },

    async showHelp() {
      await this.typeText('info', 'ÂèØÁî®ÂëΩ‰ª§ÂàóË°®:')
      await this.sleep(200)
      await this.typeText('success', '  help          - ÊòæÁ§∫Ê≠§Â∏ÆÂä©‰ø°ÊÅØ')
      await this.typeText('success', '  hack [target] - Ê®°ÊãüÈªëÂÆ¢ÊîªÂáª')
      await this.typeText('success', '  scan [ip]     - Ê®°ÊãüÁΩëÁªúÊâ´Êèè')
      await this.typeText('success', '  matrix        - ÊòæÁ§∫Áü©ÈòµÈõ®ÊïàÊûú')
      await this.typeText('success', '  system        - ÊòæÁ§∫Á≥ªÁªü‰ø°ÊÅØ')
      await this.typeText('success', '  download [file] - Ê®°ÊãüÊñá‰ª∂‰∏ãËΩΩ')
      await this.typeText('success', '  encrypt [text] - Ê®°ÊãüÂä†ÂØÜËøáÁ®ã')
      await this.typeText('success', '  clear         - Ê∏ÖÂ±è')
    },

    async simulateHack(target) {
      await this.typeText('warning', `Ê≠£Âú®ÂàùÂßãÂåñÊîªÂáªÊ®°Âùó...`)
      await this.sleep(1000)
      await this.typeText('info', `ÁõÆÊ†á: ${target}`)
      await this.typeText('info', `Ê≠£Âú®Êâ´ÊèèÁ´ØÂè£...`)

      const ports = [21, 22, 23, 25, 53, 80, 110, 443, 993, 995]
      for (const port of ports) {
        await this.typeText('success', `Á´ØÂè£ ${port}: ÂºÄÊîæ`)
        await this.sleep(300)
      }

      await this.typeText('warning', `Ê≠£Âú®Â∞ùËØïÊö¥ÂäõÁ†¥Ëß£...`)
      await this.sleep(2000)
      await this.typeText('success', `‚úì Ëé∑ÂæóÁÆ°ÁêÜÂëòÊùÉÈôê`)
      await this.typeText('success', `‚úì Ê§çÂÖ•ÂêéÈó®Á®ãÂ∫è`)
      await this.typeText('success', `‚úì Êï∞ÊçÆÊèêÂèñÂÆåÊàê`)
      await this.typeText('success', `ÊîªÂáªÊàêÂäü! ÁõÆÊ†áÂ∑≤Ë¢´ÊéßÂà∂`)
    },

    async simulateScan(ip) {
      await this.typeText('info', `Ê≠£Âú®Êâ´Êèè ${ip}...`)
      await this.sleep(500)

      const services = [
        'SSH (22/tcp)',
        'HTTP (80/tcp)',
        'HTTPS (443/tcp)',
        'FTP (21/tcp)',
        'SMTP (25/tcp)'
      ]

      for (const service of services) {
        await this.typeText('success', `ÂèëÁé∞ÊúçÂä°: ${service}`)
        await this.sleep(400)
      }

      await this.typeText('warning', `Ê£ÄÊµãÂà∞Èò≤ÁÅ´Â¢ô...`)
      await this.sleep(1000)
      await this.typeText('success', `Êâ´ÊèèÂÆåÊàê! ÂèëÁé∞ ${services.length} ‰∏™ÂºÄÊîæÁ´ØÂè£`)
    },

    async showMatrix() {
      await this.typeText('success', `ÂêØÂä®Áü©ÈòµÊ®°Âºè...`)
      await this.sleep(1000)

      const matrixChars = '01„Ç¢„Ç§„Ç¶„Ç®„Ç™„Ç´„Ç≠„ÇØ„Ç±„Ç≥„Çµ„Ç∑„Çπ„Çª„ÇΩ„Çø„ÉÅ„ÉÑ„ÉÜ„Éà„Éä„Éã„Éå„Éç„Éé„Éè„Éí„Éï„Éò„Éõ„Éû„Éü„É†„É°„É¢„É§„É¶„É®„É©„É™„É´„É¨„É≠„ÉØ„É≤„É≥'

      for (let i = 0; i < 10; i++) {
        let line = ''
        for (let j = 0; j < 60; j++) {
          line += matrixChars[Math.floor(Math.random() * matrixChars.length)]
        }
        await this.typeText('success', line)
        await this.sleep(100)
      }

      await this.typeText('warning', `Áü©ÈòµËøûÊé•Â∑≤Âª∫Á´ã...`)
    },

    async showSystemInfo() {
      await this.typeText('info', `Ê≠£Âú®Ëé∑ÂèñÁ≥ªÁªü‰ø°ÊÅØ...`)
      await this.sleep(1000)

      const info = [
        `Êìç‰ΩúÁ≥ªÁªü: HackOS v3.14.159`,
        `ÂÜÖÊ†∏ÁâàÊú¨: Linux 5.15.0-hackbox`,
        `CPU: Intel Core i9-9900K @ 3.60GHz`,
        `ÂÜÖÂ≠ò: 32GB DDR4`,
        `ÊòæÂç°: NVIDIA GeForce RTX 3080`,
        `ÁΩëÁªú: ÂçÉÂÖÜ‰ª•Â§™ÁΩë`,
        `ÂÆâÂÖ®Á∫ßÂà´: MAXIMUM`,
        `Âä†ÂØÜÁä∂ÊÄÅ: AES-256 Â∑≤ÂêØÁî®`,
        `Èò≤ÁÅ´Â¢ô: Â∑≤ÊøÄÊ¥ª`,
        `ÂÖ•‰æµÊ£ÄÊµã: ËøêË°å‰∏≠`
      ]

      for (const item of info) {
        await this.typeText('success', item)
        await this.sleep(300)
      }
    },

    async simulateDownload(filename) {
      await this.typeText('info', `Ê≠£Âú®‰∏ãËΩΩ ${filename}...`)
      await this.sleep(500)

      const progress = [10, 25, 45, 60, 75, 85, 95, 100]
      for (const p of progress) {
        const bar = '‚ñà'.repeat(Math.floor(p / 5)) + '‚ñë'.repeat(20 - Math.floor(p / 5))
        await this.typeText('warning', `[${bar}] ${p}%`)
        await this.sleep(400)
      }

      await this.typeText('success', `‚úì ${filename} ‰∏ãËΩΩÂÆåÊàê`)
      await this.typeText('info', `Êñá‰ª∂Â§ßÂ∞è: ${Math.floor(Math.random() * 1000)}MB`)
    },

    async simulateEncrypt(text) {
      await this.typeText('info', `Ê≠£Âú®Âä†ÂØÜ: "${text}"`)
      await this.sleep(500)

      const steps = [
        'ÂàùÂßãÂåñÂä†ÂØÜÂºïÊìé...',
        'ÁîüÊàêÈöèÊú∫ÂØÜÈí•...',
        'Â∫îÁî®AES-256Âä†ÂØÜ...',
        'Ê∑ªÂä†Êï∞Â≠óÁ≠æÂêç...',
        'È™åËØÅÂÆåÊï¥ÊÄß...'
      ]

      for (const step of steps) {
        await this.typeText('warning', step)
        await this.sleep(600)
      }

      const encrypted = btoa(text).split('').reverse().join('')
      await this.typeText('success', `Âä†ÂØÜÁªìÊûú: ${encrypted}`)
    },

    clearTerminal() {
      this.outputLines = []
      this.initTerminal()
    },

    previousCommand() {
      if (this.historyIndex < this.commandHistory.length - 1) {
        this.historyIndex++
        this.currentCommand = this.commandHistory[this.historyIndex] || ''
      }
    },

    nextCommand() {
      if (this.historyIndex > 0) {
        this.historyIndex--
        this.currentCommand = this.commandHistory[this.historyIndex] || ''
      } else if (this.historyIndex === 0) {
        this.historyIndex = -1
        this.currentCommand = ''
      }
    },

    changeTheme(themeName) {
      const themes = {
        matrix: {
          '--primary-color': '#00ff00',
          '--secondary-color': '#008800',
          '--background-color': '#000000',
          '--text-color': '#00ff00'
        },
        hacker: {
          '--primary-color': '#00ff00',
          '--secondary-color': '#ff0000',
          '--background-color': '#000000',
          '--text-color': '#00ff00'
        },
        cyberpunk: {
          '--primary-color': '#ff00ff',
          '--secondary-color': '#00ffff',
          '--background-color': '#0a0a0a',
          '--text-color': '#ff00ff'
        },
        retro: {
          '--primary-color': '#ffb000',
          '--secondary-color': '#ff8800',
          '--background-color': '#1a1a00',
          '--text-color': '#ffb000'
        },
        neon: {
          '--primary-color': '#00ffff',
          '--secondary-color': '#ff0080',
          '--background-color': '#000020',
          '--text-color': '#00ffff'
        }
      }

      const theme = themes[themeName]
      if (theme) {
        const root = document.documentElement
        Object.keys(theme).forEach(key => {
          root.style.setProperty(key, theme[key])
        })
      }
    },

    async startAutoDemo() {
      if (this.autoDemo) return
      this.autoDemo = true

      let commandIndex = 0
      this.autoDemoInterval = setInterval(async () => {
        if (!this.autoDemo || this.isTyping) return

        const command = this.demoCommands[commandIndex]
        this.currentCommand = command
        this.executeCommand()

        commandIndex = (commandIndex + 1) % this.demoCommands.length
      }, 8000)
    },

    stopAutoDemo() {
      this.autoDemo = false
      if (this.autoDemoInterval) {
        clearInterval(this.autoDemoInterval)
        this.autoDemoInterval = null
      }
    }
  }
}
</script>

